AAEA;EACA,eAAA;EACA,gBAAA;CACA","file":"shoppingcar.vue","sourcesContent":["<template>\n    <div class=\"shoppingcar\">\n\n      <el-container direction=\"vertical\">\n        <!--   头部和banner-->\n        <el-main><pageheader></pageheader></el-main>\n        <div class=\"banner\">\n          <el-container direction=\"horizontal\" class=\"search\">\n            <el-main>\n              <el-image :src=\"require('@/assets/img/logo.png')\" style=\"width: 230px;height: 88px\"></el-image>\n            </el-main>\n            <el-container direction=\"vertical\">\n              <el-main class=\"search-input\">\n                <el-input placeholder=\"请输入内容\" style=\"width:320px\"></el-input> <el-button><span>搜索</span></el-button></el-main>\n              <el-main class=\"history\">\n                <ul class=\"history\">\n                  <li class=\"his-li\">莫言</li>\n                  <li class=\"his-li\">余光中</li>\n                  <li class=\"his-li\">毛姆</li>\n                  <li class=\"his-li\">追风筝的人</li>\n                  <li class=\"his-li\">莫言</li>\n                  <li class=\"his-li\">余光中</li>\n                </ul>\n\n              </el-main>\n            </el-container>\n          </el-container>\n        </div>\n        <!--购物车-->\n        <el-main style=\"width: 100%;\">\n        <div class=\"car\" style=\"width: 1266px;margin: 0 auto\">\n          <el-table\n            ref=\"multipleTable\"\n            :data=\"this.$store.state.cartList\"\n            tooltip-effect=\"dark\"\n            style=\"width: 100%\"\n            @selection-change=\"handleSelectionChange\">\n            <el-table-column\n              type=\"selection\"\n              width=\"55\"\n              >\n            </el-table-column>\n            <el-table-column\n              label=\"商品信息\"\n              width=\"120\">\n              <template slot-scope=\"scope\">\n                <el-image\n                  style=\"width: 60px; height: 60px\"\n                  :src=\"'http://localhost:88/upload/'+scope.row.book.imgurl[0]\"\n                >\n                </el-image>\n\n              </template>\n            </el-table-column>\n            <el-table-column\n              label=\"书名\"\n              width=\"190\">\n              <template slot-scope=\"scope\">\n                <span>{{scope.row.book.bookName}}</span><!--书名-->\n              </template>\n            </el-table-column>\n\n            <el-table-column\n              label=\"单价\"\n              width=\"120\">\n              <template slot-scope=\"scope\">{{scope.row.book.price*scope.row.book.discount}}</template>\n            </el-table-column>\n            <el-table-column\n              label=\"数量\"\n              show-overflow-tooltip>\n              <template slot-scope=\"scope\">\n                <el-input-number v-model=\"scope.row.buyNum\" @click=\"handleCount(scope.row.index)\" :min=\"1\" :max=\"scope.row.book.storage\" label=\"\"></el-input-number>\n               </template>\n            </el-table-column>\n            <el-table-column\n              label=\"小计\"\n              show-overflow-tooltip>\n              <template slot-scope=\"scope\">{{scope.row.book.price*scope.row.book.discount*scope.row.buyNum }}</template>\n            </el-table-column>\n            <el-table-column\n              label=\"管理\"\n              show-overflow-tooltip>\n              <template slot-scope=\"scope\">\n                <span @click=\"toDeletecar(scope.row.carId)\">删除</span>\n              </template>\n            </el-table-column>\n          </el-table>\n          <div style=\"margin-top: 20px\">\n            <el-button @click=\"deleteCarList\" style=\"display: block\"><i class=\"el-icon-delete\"></i></el-button>\n            <el-pagination\n              @size-change=\"handleSizeChange\"\n              @current-change=\"handleCurrentChange\"\n              :current-page.sync=\"pageInfo.currPage\"\n              background\n              :page-size=\"5\"\n              layout=\"total, prev, pager, next\"\n              :total=\"this.pageInfo.totalCount\">\n            </el-pagination>\n          </div>\n\n          <div class=\"cart-footer\" v-show=\"cartList.length\">\n            <div class=\"cart-footer-desc\">\n              共计 <span>{{ this.multipleSelection.length }}</span> 件商品\n            </div>\n            <div class=\"cart-footer-desc\">\n              应付总额 <span>¥ {{this.costAll}}</span>\n            </div>\n          <div style=\"margin-top: 20px\">\n            <el-button class=\"buy-btn\" @click=\"carSubmit\">确定购买</el-button>\n          </div>\n<!--\n          <div class=\"cart-header\">\n            <div class=\"cart-header-title\">购物清单</div>\n            <div class=\"cart-header-main\">\n              <div class=\"cart-check\"><el-checkbox>全选</el-checkbox></div>\n              <div class=\"cart-info\">商品信息</div>\n              <div class=\"cart-price\">单价</div>\n              <div class=\"cart-count\">数量</div>\n              <div class=\"cart-cost\">小计</div>\n              <div class=\"cart-delete\">删除</div>\n            </div>\n          </div>\n          <div class=\"cart-content\">\n            <div class=\"cart-content-main\" v-for=\"cart in cartList\" :key=\"cart.index\">\n              <div class=\"cart-info\">\n                <div class=\"cart-check\" style=\"margin-left: 32px\">\n                  <el-checkbox v-model=\"cart.carId\"></el-checkbox></div>\n              &lt;!&ndash;  <img :src=\"'http://localhost:88/upload/'+cart.book.imgurl[0]\">&ndash;&gt;\n                <el-image\n                  style=\"width: 60px; height: 60px\"\n                  :src=\"'http://localhost:88/upload/'+cart.book.imgurl[0]\"\n                  >\n                </el-image>\n                <span>{{cart.book.bookName}}</span>&lt;!&ndash;书名&ndash;&gt;\n              </div>\n              <div class=\"cart-price\" style=\"margin-left: 120px\"><span>¥{{cart.book.price*cart.book.discount}}</span></div>&lt;!&ndash;价格&ndash;&gt;\n              <div class=\"cart-count\">\n                <span class=\"cart-control-minus\">-</span> {{ cart.buyNum }}&lt;!&ndash;@click=\"handleCount(index, -1)\"&ndash;&gt;\n                <span class=\"cart-control-add\">+</span>&lt;!&ndash; @click=\"handleCount(index, 1)\"&ndash;&gt;\n              </div>\n              <div class=\"cart-cost\"><span>¥{{cart.book.price*cart.book.discount*cart.buyNum}}</span></div>&lt;!&ndash;小计，价格*数量&ndash;&gt;\n              <div class=\"cart-delete\">\n                <span class=\"cart-control-delete\" >删除</span>&lt;!&ndash;@click=\"handleDelete(index)\"&ndash;&gt;\n              </div>\n            </div>\n            <div class=\"cart-empty\" v-if=\"!cartList.length\">购物车为空</div>\n          </div>\n\n          <div class=\"cart-footer\" v-show=\"cartList.length\">\n            <div class=\"cart-footer-desc\">\n              共计 <span>{{ 4 }}</span> 件商品\n            </div>\n            <div class=\"cart-footer-desc\">\n              应付总额 <span>¥ {{ 233  }}</span>\n\n            </div>\n            <div class=\"cart-footer-desc\">\n              <div class=\"cart-control-order\">下单</div>&lt;!&ndash; @click=\"handleOrder\"&ndash;&gt;\n            </div>\n          </div>-->\n        </div>\n        </div>\n        </el-main>\n        <pagefooter></pagefooter>\n      </el-container>\n    </div>\n</template>\n\n<script>\n    import pageheader from \"../components/pageheader\";\n    import pagefooter from \"../components/footer\";\n    import {findcar,toorder} from \"../axios/api\";\n\n    export default {\n        name: \"shoppingcar\",\n      components: {\n        pageheader,\n        pagefooter\n      },\n      data:function () {\n        return{\n          cartList:[],\n          multipleSelection: [],//选中的删除\n/*          currentPage:1 ,*/\n          buyBooks:[],\n          pageInfo:{\n            pageSize:0,\n            totalPage:1,\n            currPage:1\n          },\n          costAll:0.0,\n          carvo:{\n            userId:0,\n            current:0\n          }\n        }\n      },\n      mounted() {\n          if(this.$store.state.user.userId!=null){\n            this.carvo.userId=this.$store.state.user.userId;\n            this.carvo.current=this.pageInfo.currPage;\n            var params=this.carvo;\n          findcar(params).then(res=>{\n            /*console.log(res.data);\n            this.cartList=res.data;*/\n\n            console.log(res.data)\n            var imglist=[];\n            res.data.lists.forEach(data=>{\n              imglist=data.book.imgurl.split(\",\");\n              data.book.imgurl=imglist\n            })\n            this.cartList=res.data.lists;\n            this.pageInfo.totalPage=res.data.totalPage;\n            this.pageInfo.pageSise=res.data.pageSize;\n            this.pageInfo.totalCount=res.data.totalCount;\n            this.pageInfo.currPage=res.data.currPage;\n            this.$store.commit('setCartList',this.cartList)\n          })\n          }\n      },\n      methods: {\n        handleSizeChange(val) {\n          console.log(`每页 ${val} 条`);\n        },\n        handleCurrentChange(val) {\n          console.log(`当前页: ${val}`);\n          this.carvo.userId=this.$store.state.user.userId;\n          this.carvo.current=val;\n          var params=this.carvo;\n          findcar(params).then(res=>{\n            /*console.log(res.data);\n            this.cartList=res.data;*/\n\n            console.log(res.data)\n            var imglist=[];\n            res.data.lists.forEach(data=>{\n              imglist=data.book.imgurl.split(\",\");\n              data.book.imgurl=imglist\n            })\n            this.cartList=res.data.lists;\n            this.pageInfo.currPage=res.data.currPage;\n            this.$store.commit('setCartList',this.cartList)\n          })\n        },\n        toggleSelection(rows) {\n          if (rows) {\n            rows.forEach(row => {\n              this.$refs.multipleTable.toggleRowSelection(row);\n            });\n          } else {\n            this.$refs.multipleTable.clearSelection();\n          }\n        },\n        handleSelectionChange(val) {\n          var cost=0;\n          this.multipleSelection = []\n          val.forEach(item => {\n            this.multipleSelection.push(item.carId);   //把所有的id放进multipleSelection\n            cost+= item.book.price * item.book.discount * item.buyNum;//计算总价\n          })\n          this.costAll=cost;\n          console.log(JSON.stringify(this.multipleSelection))\n        },\n        toDeletecar(carId) {\n          this.$store.commit('deleteCart', carId);\n        },\n        deleteCarList() {\n          console.log(this.multipleSelection)\n          var carIds = this.multipleSelection;\n          carIds.forEach(id => {\n            this.$store.commit('deleteCart', id);\n          })\n        },\n        handleCount(index) {\n          this.$store.commit('editCartCount', {\n            carId: this.cartList[index].id,\n            buyNum: this.cartList[index].buyNum\n          });\n        },\n        carSubmit() {\n/*          this.buyBooks.userId;\n          this.buyBooks.bookIds = this.multipleSelection;\n          this.buyBooks.createDate = new Date();\n          this.buyBooks.amount=this.costAll;*/\n          var carIds=this.multipleSelection;\n          toorder(carIds).then(res=>{\n           console.log( res.data);\n           this.$router.push({\n             name:\"userorder\",\n             params:{\n               orderlist:res.data\n             }\n           })\n          })\n\n        }\n        /*      handleDelete(index) {\n          this.$store.commit('deleteCart', this.cartList[index].id);\n        },*/\n         }\n\n    }\n</script>\n\n<style scoped>\n  .buy-btn{\n    margin-left: 920px;;\n  }\n  .search{\n    width: 1226px;\n    margin: 0 auto;\n  }\n  .history{\n    position: relative;\n    margin-left: 210px;\n    padding-top: 0px;\n  }\n  .banner{\n    width: 100%;\n    background-color: #E9EBE7;\n    border: 1px solid #E9EBE7;\n  }\n  .search-input{\n    margin-left:450px;\n    margin-top: 2px;\n  }\n  el-input{\n    border:2px solid #E0B41B;\n    border-radius: 100%;\n  }\n  .his-li{\n    float: left;\n    color: #333333;\n    font-size: 12pt;\n    margin-left: 8px;\n    opacity: 0.8;\n  }\n  .cart {\n    margin: 32px;\n    background: #fff;\n    border: 1px solid #dddee1;\n    border-radius: 10px;\n  }\n\n  .cart-header-title {\n    padding: 16px 32px;\n    border-bottom: 1px solid #dddee1;\n    border-radius: 10px 10px 0 0;\n    background: #f8f8f9;\n  }\n\n  .cart-header-main {\n    padding: 10px 32px;\n    overflow: hidden;\n    border-bottom: 1px solid #dddee1;\n    background: #eee;\n    overflow: hidden;\n  }\n\n  .cart-empty {\n    text-align: center;\n    padding: 32px;\n  }\n\n  .cart-header-main div {\n    text-align: center;\n    float: left;\n    font-size: 14px;\n  }\n\n  div.cart-info {\n    width:50%;\n    text-align: left;\n  }\n\n  .cart-price,.cart-check{\n    width: 10%;\n  }\n\n  .cart-count {\n    width: 10%;\n  }\n\n  .cart-cost {\n    width: 10%;\n  }\n\n  .cart-delete {\n    width: 10%;\n  }\n\n  .cart-content-main {\n    padding: 0 32px;\n    height: 60px;\n\n    line-height: 60px;\n    text-align: center;\n    border-bottom: 1px dashed #e9eaec;\n    overflow: hidden;\n  }\n\n  .cart-content-main div {\n    float: left;\n  }\n\n  .cart-content-main img {\n    width: 60px;\n    height: 60px;\n    position: relative;\n    top: 10px;\n  }\n\n  .cart-control-minus,\n  .cart-control-add {\n    display: inline-block;\n    margin: 0 4px;\n    width: 24px;\n    height: 24px;\n    line-height: 22px;\n    text-align: center;\n    background: #f8f8f9;\n    border-radius: 50%;\n    box-shadow: 0 1px 1px rgba(0, 0, 0, .2);\n    cursor: pointer;\n  }\n\n  .cart-control-delete {\n    cursor: pointer;\n    color: #2d8cf0;\n  }\n\n  .cart-control-order {\n    display: inline-block;\n    padding: 8px 32px;\n    border-radius: 6px;\n    background: #2d8cf0;\n    color: #fff;\n    cursor: pointer;\n  }\n\n  .cart-footer {\n    padding: 32px;\n    text-align: right;\n  }\n\n  .cart-footer-desc {\n    display: inline-block;\n    padding: 0 16px;\n  }\n\n  .cart-footer-desc span {\n    color: #f2352e;\n    font-size: 20px;\n  }\n</style>\n"],"sourceRoot":""}]);

