]{\n  margin-left:450px;\n  margin-top: 2px;\n}\n.his-li[data-v-6a47d79e]{\n  float: left;\n  color: #333333;\n  font-size: 12pt;\n  margin-left: 8px;\n  opacity: 0.8;\n}\n.book-tabs[data-v-6a47d79e] .el-tabs__item.is-active{\n    color: #E0B41B !important;\n}\n.book-tabs[data-v-6a47d79e] .el-tabs__item:hover{\n    color: #E0B41B;\n}\n.book-tabs[data-v-6a47d79e] .el-tabs__item{\n    font-size: 18px;\n    margin:0  15px;\n}\n", "", {"version":3,"sources":["C:/StudyProject/VueProjects/ui-bookstore/src/page/src/page/bookdetail.vue"],"names":[],"mappings":";AAsQA;IACA,aAAA;IACA,aAAA;CACA;AACA;IACA,aAAA;IACA,eAAA;IACA,cAAA;CACA;AAEA;IACA,aAAA;IACA,cAAA;CACA;AACA;IACA,yBAAA;IACA,0BAAA;IACA,aAAA;IACA,aAAA;IACA,iBAAA;CACA;AACA;IACA,wBAAA;IACA,eAAA;CACA;AACA;IACA,cAAA;CACA;AACA;IACA,aAAA;IACA,aAAA;IACA,0BAAA;IACA,0BAAA;IACA,mBAAA;CACA;AACA;IACA,0BAAA;CACA;AACA;IACA,cAAA;IACA,aAAA;CACA;AACA;IACA,aAAA;IACA,aAAA;IACA,aAAA;IACA,2BAAA;CAEA;AACA;IACA,YAAA;IACA,0BAAA;IACA,gBAAA;CACA;AAEA;IACA,cAAA;IACA,eAAA;IACA,yBAAA;CACA;AAEA;EACA,aAAA;EACA,cAAA;EACA,iBAAA;EACA,0BAAA;EACA,mBAAA;CACA;AACA;EACA,cAAA;EACA,YAAA;EACA,yCAAA;EAAA,iCAAA;CAEA;AACA;EACA,8BAAA;UAAA,sBAAA;CACA;AACA;EACA,cAAA;EACA,eAAA;CACA;AACA;EACA,mBAAA;EACA,mBAAA;EACA,iBAAA;CACA;AACA;EACA,YAAA;EACA,0BAAA;EACA,0BAAA;CACA;AACA;EACA,kBAAA;EACA,gBAAA;CACA;AACA;EACA,YAAA;EACA,eAAA;EACA,gBAAA;EACA,iBAAA;EACA,aAAA;CACA;AACA;IACA,0BAAA;CACA;AACA;IACA,eAAA;CACA;AACA;IACA,gBAAA;IACA,eAAA;CACA","file":"bookdetail.vue","sourcesContent":["<template>\n    <div id=\"bookdetail\">\n\n      <el-container direction=\"vertical\">\n     <!--   头部和banner-->\n        <el-main><pageheader></pageheader></el-main>\n        <div class=\"banner\">\n          <el-container direction=\"horizontal\" class=\"search\">\n            <el-main>\n              <el-image :src=\"require('@/assets/img/logo.png')\" style=\"width: 230px;height: 88px\"></el-image>\n            </el-main>\n            <el-container direction=\"vertical\">\n              <el-main class=\"search-input\">\n                <el-input placeholder=\"请输入内容\" style=\"width:320px\"></el-input> <el-button><span>搜索</span></el-button></el-main>\n              <el-main class=\"history\">\n                <ul class=\"history\">\n                  <li class=\"his-li\">莫言</li>\n                  <li class=\"his-li\">余光中</li>\n                  <li class=\"his-li\">毛姆</li>\n                  <li class=\"his-li\">追风筝的人</li>\n                  <li class=\"his-li\">莫言</li>\n                  <li class=\"his-li\">余光中</li>\n                </ul>\n\n              </el-main>\n            </el-container>\n          </el-container>\n        </div>\n<!--商品详情+购物车选项-->\n        <div  class=\"bookdetail-body\">\n          <el-button icon=\"el-icon-back\" type=\"text\"><router-link to=\"/index\" style=\"color: #333333\">返回</router-link> </el-button>\n        <el-container>\n          <el-container class=\"book-show\">\n\n              <div class=\"big-img\" v-if=\"book.imgurl\">\n                <el-image :src=\"'http://localhost:88/upload/'+book.imgurl[0]\" class=\"img\"></el-image>\n              </div>\n\n            <el-main style=\"margin-left: 20px\">\n             <span style=\"font-size: 18px\"><strong>{{book.bookName}}</strong></span>\n              <div class=\"book-div-ul\">\n                <ul class=\"book-ul\">\n                  <li>\n                    ISBN:<span>{{book.iSBN}}</span>\n                  </li>\n                  <li>\n                    作者：<span>{{book.author}}</span>\n                  </li>\n                  <li>\n                    类型：<span>{{book.code}}</span>\n                  </li>\n                  <li>\n                    出版社：<span>{{book.publisher}}</span>\n                  </li>\n                  <li>\n                    出版日期：<span>{{book.publishTime | formatDate}}</span>\n                  </li>\n                </ul>\n\n              </div>\n              <div class=\"book-price-ul\">\n                <ul>\n                  <li>\n                    售价：<span style=\"font-size: 18px;color: red\">￥{{book.price*book.discount}}</span>\n                  </li>\n                  <li>\n                    原价：<span style=\"font-size: 15px;opacity: 0.9\">{{book.price}}</span>\n                    折扣：<span style=\"font-size: 15px;opacity: 0.9\">{{book.discount}}</span>\n                  </li>\n                </ul>\n              </div>\n              <el-divider></el-divider>\n              <div class=\"book-date\">\n                <ul>\n                  <li>上新时间：<span>{{book.upDate | formatTime}}</span></li>\n                  <li>库存：<span>{{book.storage}}</span></li>\n                </ul>\n                购买数量：  <el-input-number v-model=\"car.buyNum\" @change=\"handleChange\" :min=\"1\" :max=\"book.storage\" ></el-input-number>\n              </div>\n              <div class=\"book-buy-btn\">\n                <el-button :autofocus=\"true\">立即购买</el-button>\n                <el-button :autofocus=\"true\" @click=\"addcar\"><i class=\"el-icon-shopping-cart-2\"></i>加入购物车</el-button>\n              </div>\n            </el-main>\n          </el-container>\n          <el-container direction=\"vertical\" style=\"border: 1px solid;\">\n            <el-main>\n              卖家信息1\n            </el-main>\n            <el-main>\n              卖家信息2\n            </el-main>\n          </el-container>\n        </el-container>\n<!--        商品详情+评价-->\n        <el-main class=\"book-tabs\">\n            <el-tabs v-model=\"activeName\" type=\"card\" @tab-click=\"handleClick\">\n              <!--商品详情页-->\n              <el-tab-pane label=\"商品详情\" name=\"first\">\n                <div><span>{{book.tips}}</span></div>\n                <div v-if=\"book.imgurl\">\n                  <div v-for=\"img in book.imgurl\" :key=\"img.index\">\n                    <el-image :src=\"'http://localhost:88/upload/'+img\" style=\"width: 300px;height: 300px;margin: 10px 40px\"></el-image>\n                  </div>\n                  </div>\n              </el-tab-pane>\n              <!--留言板页-->\n              <el-tab-pane label=\"留言板\" name=\"second\">\n                  <div class=\"comment-group\">\n                    <div class=\"addComment\">\n                      <el-form ref=\"addcomment\" :model=\"addcomment\">\n                        <el-avatar :src=\"this.$store.state.user.userImg\" style=\"border: 1px solid #E0B41B\"></el-avatar>\n                        <el-form-item label=\"\">\n                          <el-input v-model=\"addcomment.content\"\n                                    type=\"textarea\"\n                                    :rows=\"2\"\n                                    placeholder=\"看对眼就留言，问问细节吧\"\n                          style=\"width: 600px\"></el-input>\n                        </el-form-item>\n                        <el-form-item>\n                          <el-button type=\"primary\" @click=\"subComment\" style=\"background-color: #E0B41B;border: 0\">留言</el-button>\n                        </el-form-item>\n                      </el-form>\n\n                    </div>\n                    <div class=\"comment\" v-for=\"(commentdata) in commentlist\" :key=\"commentdata.index\">\n                      <el-container>\n                          <el-avatar :src=\"'http://localhost:88/upload/'+commentdata.user.userImg\" style=\"border: 1px solid #E0B41B\"></el-avatar>\n                      <el-main style=\"margin-left: 12px\">\n                        <div class=\"commit-name\">\n                          <span style=\"font-size: 14px\">{{commentdata.user.userName}}</span>\n                        <!--  <el-rate\n                            v-model=\"commentdata.star\"\n                            disabled\n                            show-score\n                            text-color=\"#ff9900\"\n                            score-template=\"{value}\">\n                          </el-rate>-->\n                          <div style=\"margin: 10px 0\">\n                            <span>{{commentdata.content}}</span>\n                          </div>\n                          <div style=\"margin: 10px 0;font-size: 14px\">\n                            <span>{{commentdata.commentDate | formatTime}}</span>\n                          </div>\n\n                        </div>\n                      </el-main>\n                      </el-container>\n                      <el-divider></el-divider>\n                    </div>\n                  </div>\n              </el-tab-pane>\n            </el-tabs>\n        </el-main>\n        </div>\n        <el-main><pagefooter></pagefooter></el-main>\n      </el-container>\n\n    </div>\n</template>\n\n<script>\n  import pageheader from '../components/pageheader'\n  import pagefooter from '../components/footer'\n  import {findbookbyid,findbookcomments,addcomment,addcar} from \"../axios/api\";\n\n  export default {\n        name: \"bookdetail\",\n       data: function () {\n          return {\n            car:{\n              buyNum:\"\"\n            },\n            book:\"\",\n            commentlist:\"\",\n            activeName: 'first',\n            addcomment: {\n              content:\"\"\n            },\n            bookId:'',\n          }\n      },\n      components: {\n        pageheader,\n        pagefooter\n      },\n      mounted() {\n          //获取图书信息\n          this.bookId=this.$route.params.bookId;//参数\n        if(this.bookId==null){\n          this.bookId=14;//测试\n        }\n           // bookId=14;//测试\n        var bid=this.bookId;\n        findbookbyid(bid).then(res=>{\n          //this.bookinfo=res;\n          let imglist=res.data.imgurl.split(\",\")//从1开始取值\n          this.book=res.data;\n          this.book.imgurl=imglist;\n          console.log(this.book)\n          //获取图书留言\n          this.getCommentlist();\n\n        })\n      },\n      methods: {\n        handleChange(value) {\n          this.car.buyNum=value;\n        },\n        handleClick(tab, event) {\n          console.log(tab, event);\n        },\n        addcar(){\n            this.car.bookId=this.bookId;\n            this.car.userId=this.$store.state.user.userId;\n            console.log(JSON.stringify(this.car))\n              addcar(this.car).then(res=> {\n              if(res!=0) {\n              this.$message({\n                showClose:true,\n                message:\"添加成功\",\n                type:\"success\"\n              })\n              this.$router.push(\"/shoppingcar\");\n            }\n          })\n\n          /*this.$store.commit('addCart', 1);\n          this.$router.push(\"/shoppingcar\");*/\n        },\n        subComment(){\n          this.addcomment.bookId=this.bookId;\n          this.addcomment.userId=this.$store.state.user.userId;\n          this.addcomment.commentDate=new Date();\n          addcomment(this.addcomment).then(res=>{\n            if(res!=0){\n              this.$message({\n                message:\"留言成功\",\n                showClose:true,\n                type:\"success\"\n              })\n              this.addcomment=\"\";\n              this.getCommentlist()\n\n            }\n          })\n          console.log(JSON.stringify(this.addcomment));\n        },\n        getCommentlist(){\n          //获取图书留言\n          var bid2=this.bookId;\n          findbookcomments(bid2).then(res=>{\n            this.commentlist=res.data;\n            console.log(res);\n          })\n        }\n\n      }\n    }\n</script>\n\n<style scoped>\n  .comment-group{\n    height: 100%;\n    width: 800px;\n  }\n  .comment,.addComment{\n    width: 700px;\n    margin: 0 auto;\n    height: 180px;\n  }\n\n  .book-show{\n    width: 800px;\n    height: 450px;\n  }\n  .book-buy-btn .el-button--default{\n    border:1px solid #E0B41B;\n    background-color: #f2f3ee;\n    width: 180px;\n    height: 45px;\n    margin: 0px 10px;\n  }\n  .el-button--default:hover{\n    background-color: white;\n    color: #E0B41B;\n  }\n  .el-divider--horizontal{\n    margin: 8px 0;\n  }\n  .book-price-ul{\n    height: 80px;\n    width: 540px;\n    border: 1px solid #F3F0E9;\n    background-color: #F3F0E9;\n    border-radius: 5px;\n  }\n  .book-price-ul  ul  li{\n    margin:10px 10px 10px 0px;\n  }\n  .book-div-ul{\n    height: 120px;\n    width: 540px;\n  }\n  .book-ul{\n    padding: 4px;\n    height: 80px;\n    width: 520px;\n    border: 1px solid  #F3F0E9;\n\n  }\n  .book-ul > li{\n    float: left;\n    margin:10px 10px 10px 0px;\n    font-size: 14px;\n  }\n\n  .bookdetail-body{\n    width: 1226px;\n    margin: 0 auto;\n    background-color:#F7F7F6; ;\n  }\n\n.big-img {\n  width: 250px;\n  height: 300px;\n  overflow: hidden;\n  border: 4px solid #cdcdcd;\n  border-radius: 8px;\n}\n.big-img .img {\n  margin:auto 0;\n  width: 100%;\n  transition: 0.5s all ease-in-out;\n\n}\n.big-img:hover .img {\n  transform: scale(1.5);\n}\n.search{\n  width: 1226px;\n  margin: 0 auto;\n}\n.history{\n  position: relative;\n  margin-left: 210px;\n  padding-top: 0px;\n}\n.banner{\n  width: 100%;\n  background-color: #E9EBE7;\n  border: 1px solid #E9EBE7;\n}\n.search-input{\n  margin-left:450px;\n  margin-top: 2px;\n}\n.his-li{\n  float: left;\n  color: #333333;\n  font-size: 12pt;\n  margin-left: 8px;\n  opacity: 0.8;\n}\n  .book-tabs >>> .el-tabs__item.is-active{\n    color: #E0B41B !important;\n  }\n  .book-tabs >>> .el-tabs__item:hover{\n    color: #E0B41B;\n  }\n  .book-tabs >>> .el-tabs__item{\n    font-size: 18px;\n    margin:0  15px;\n  }\n</style>\n\n<!--<style>\n  .el-tabs__item.is-active{\n    color: #E0B41B !important;\n  }\n  .el-tabs__item:hover{\n    color: #E0B41B;\n  }\n  .el-tabs__item{\n    font-size: 18px;\n    margin:0  15px;\n  }\n\n\n\n</style>-->\n"],"sourceRoot":""}]);

