<template>
    <div id="userorder">

      <el-container direction="vertical">
        <pageheader></pageheader>
        <el-main style="width: 100%;">
          <h3>收货地址</h3>
          <el-form :model="addrForm" status-icon ref="addrForm" class="address-form" :inline="true">
            <el-form-item label="姓名">
              <el-input v-model="addrForm.account"></el-input>
            </el-form-item>
            <el-form-item label="电话">
              <el-input v-model.number="addrForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="地址">
              <el-input v-model="addrForm.email"></el-input>
            </el-form-item>

          </el-form>
        </el-main>
          <el-main style="width: 100%;">
            <h3>订单详情</h3>
            <div class="car" style="width: 1266px;margin: 0 auto">

              <el-table
                ref="multipleTable"
                :data="orderitem"
                tooltip-effect="dark"
                style="width: 100%"
               >
                <el-table-column
                  label="商品信息"
                  width="120">
                  <template slot-scope="scope">
                    <el-image
                      style="width: 60px; height: 60px"
                      :src="'http://localhost:88/upload/'+scope.row.book.imgurl[0]"
                    >
                    </el-image>
                    <span>{{scope.row.book.iSBN}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="书名|作者"
                  show-overflow-tooltip>
                  <template slot-scope="scope">
                    <span>《{{scope.row.book.bookName}}》| <span>{{scope.row.book.author}}</span></span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="出版社"
                  show-overflow-tooltip>
                  <template slot-scope="scope">
                    <span>{{scope.row.book.publisher}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="单价"
                  width="120">
                  <template slot-scope="scope">{{scope.row.book.price*scope.row.book.discount}}</template>
                </el-table-column>
                <el-table-column
                  label="数量"
                  show-overflow-tooltip>
                  <template slot-scope="scope">
                    {{scope.row.buyNum}}
                  </template>
                </el-table-column>
                <el-table-column
                  label="小计"
                  show-overflow-tooltip>
                  <template slot-scope="scope">{{scope.row.book.price*scope.row.book.discount*scope.row.buyNum }}</template>
                </el-table-column>
              </el-table>

              <div class="cart-footer" v-show="orderitem.length">
                <div class="cart-footer-desc">
                  共计 <span>{{countAll}}</span> 件商品
                </div>
                <div class="cart-footer-desc">
                  应付总额 <span>¥ {{costAll}}</span>
                </div>
                <div style="margin-top: 20px">
                  <el-button class="buy-btn" @click="submitOrder">提价订单</el-button>
                </div>
                <!--
                          <div class="cart-header">
                            <div class="cart-header-title">购物清单</div>
                            <div class="cart-header-main">
                              <div class="cart-check"><el-checkbox>全选</el-checkbox></div>
                              <div class="cart-info">商品信息</div>
                              <div class="cart-price">单价</div>
                              <div class="cart-count">数量</div>
                              <div class="cart-cost">小计</div>
                              <div class="cart-delete">删除</div>
                            </div>
                          </div>
                          <div class="cart-content">
                            <div class="cart-content-main" v-for="cart in cartList" :key="cart.index">
                              <div class="cart-info">
                                <div class="cart-check" style="margin-left: 32px">
                                  <el-checkbox v-model="cart.carId"></el-checkbox></div>
                              &lt;!&ndash;  <img :src="'http://localhost:88/upload/'+cart.book.imgurl[0]">&ndash;&gt;
                                <el-image
                                  style="width: 60px; height: 60px"
                                  :src="'http://localhost:88/upload/'+cart.book.imgurl[0]"
                                  >
                                </el-image>
                                <span>{{cart.book.bookName}}</span>&lt;!&ndash;书名&ndash;&gt;
                              </div>
                              <div class="cart-price" style="margin-left: 120px"><span>¥{{cart.book.price*cart.book.discount}}</span></div>&lt;!&ndash;价格&ndash;&gt;
                              <div class="cart-count">
                                <span class="cart-control-minus">-</span> {{ cart.buyNum }}&lt;!&ndash;@click="handleCount(index, -1)"&ndash;&gt;
                                <span class="cart-control-add">+</span>&lt;!&ndash; @click="handleCount(index, 1)"&ndash;&gt;
                              </div>
                              <div class="cart-cost"><span>¥{{cart.book.price*cart.book.discount*cart.buyNum}}</span></div>&lt;!&ndash;小计，价格*数量&ndash;&gt;
                              <div class="cart-delete">
                                <span class="cart-control-delete" >删除</span>&lt;!&ndash;@click="handleDelete(index)"&ndash;&gt;
                              </div>
                            </div>
                            <div class="cart-empty" v-if="!cartList.length">购物车为空</div>
                          </div>

                          <div class="cart-footer" v-show="cartList.length">
                            <div class="cart-footer-desc">
                              共计 <span>{{ 4 }}</span> 件商品
                            </div>
                            <div class="cart-footer-desc">
                              应付总额 <span>¥ {{ 233  }}</span>

                            </div>
                            <div class="cart-footer-desc">
                              <div class="cart-control-order">下单</div>&lt;!&ndash; @click="handleOrder"&ndash;&gt;
                            </div>
                          </div>-->
              </div>
            </div>
          </el-main>
        <pagefooter></pagefooter>
      </el-container>
    </div>
</template>

<script>
  import {addorder} from '../axios/api'
  import pageheader from "../components/pageheader";
  import pagefooter from "../components/footer";
    export default {
        name: "userorder",
      components: {
        pageheader,
        pagefooter
      },
        data:function(){
          return{
            orderitem:[],
            addrForm:"",
            uporder:{
              carIds:[],
              userId:0,
              amount:0
            }
          }
        },
      computed:{
         countAll(){
           var count=0;
             this.orderitem.forEach(data=>{
               count+=data.buyNum
           })
           return count;
         },
        costAll(){
           var cost=0;
          this.orderitem.forEach(data=>{
            cost+=  data.book.price*data.book.discount*data.buyNum
          })
          return cost;
        },
        getCarIds(){
           var carIds=[];
          this.orderitem.forEach(data=>{
            carIds.push(data.carId)
          })
          return carIds;
        }
      },
        mounted() {
          JSON.stringify(this.$route.params.orderlist)
          let orderlist=[];
          orderlist=this.$route.params.orderlist
          orderlist.forEach(data=>{
            data.book.imgurl=data.book.imgurl.split(",");
          })
          this.orderitem=orderlist;
        },
      methods:{
        submitOrder(){
          this.uporder.carIds=this.getCarIds
          this.uporder.userId=this.$store.state.user.userId
           this.uporder.amount=this.costAll
          console.log(this.uporder)
          var params=this.uporder;
          addorder(params).then(res=>{
            console.log(res.data)
            this.$message({
              message:'下单成功'
            })
            this.$router.push({
              name:"orderlist"
            })
          })
        }
      }
    }
</script>

<style scoped>
.address-form{
  width: 1226px;
  padding-bottom: 50px;
}
  .address-form .el-form-item >>>.el-input{
    width: 300px;
  }
</style>



