<li class=\"his-li\">莫言</li>\n            <li class=\"his-li\">余光中</li>\n            <li class=\"his-li\">毛姆</li>\n            <li class=\"his-li\">追风筝的人</li>\n            <li class=\"his-li\">莫言</li>\n            <li class=\"his-li\">余光中</li>\n          </ul>\n\n        </el-main>\n      </el-container>\n    </el-container>\n  </div>\n\n<!--  添加图书-->\n  <el-container style=\"width: 1226px;margin: 0 auto\">\n    <el-main>\n      <div class=\"user-info\">\n        个人中心\n        <!--用户信息操作栏-->\n\n      </div>\n\n    </el-main>\n    <el-form :model=\"bookForm\" status-icon  ref=\"bookForm\" label-width=\"100px\" class=\"book-form\">\n      <el-form-item  label=\"ISBN\" class=\"\">\n        <el-input v-model=\"bookForm.iSBN\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"书名\">\n        <el-input v-model=\"bookForm.bookName\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"作者\">\n        <el-input v-model=\"bookForm.author\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"出版社\">\n        <el-input v-model=\"bookForm.publisher\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"出版日期\">\n        <el-date-picker\n          v-model=\"bookForm.publishTime\"\n          type=\"date\"\n          placeholder=\"选择日期\"\n          format=\"yyyy 年 MM 月 dd 日\"\n          value-format=\"yyyy-MM-dd\">\n        </el-date-picker>\n      </el-form-item>\n      <el-form-item label=\"数量\">\n        <el-input v-model.number=\"bookForm.storage\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"单价\">\n        <el-input v-model=\"bookForm.price\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"折扣\">\n        <el-input v-model=\"bookForm.discount\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"现价\">\n        <el-input v-model=\"newprice\" readonly></el-input>\n      </el-form-item>\n      <el-form-item label=\"书本描述\">\n        <el-input\n          type=\"textarea\"\n          :rows=\"4\"\n          placeholder=\"几成新呢，描述下你的书吧\"\n          v-model=\"bookForm.tips\"\n          style=\"width: 400px\"></el-input>\n      </el-form-item>\n      <el-form-item label=\"添加书本图片\">\n        <el-upload\n          action=\"\"\n          multiple\n          :data=\"bookForm\"\n          list-type=\"picture-card\"\n          :file-list=\"filelist\"\n          :auto-upload=\"false\"\n          :class=\"{hide:hideUploadAdd}\"\n          :on-change=\"handleAddChange\"\n          :on-exceed=\"handleExceed\"\n          :on-remove=\"handleRemove\"\n          ref=\"upload\"\n           >\n          <i slot=\"default\" class=\"el-icon-plus\"></i>\n          <div class=\"el-upload__tip\" slot=\"tip\">（最多上传5张图片）</div>\n          <div slot=\"file\" slot-scope=\"{file}\">\n            <img\n              class=\"el-upload-list__item-thumbnail\"\n              :src=\"file.url\" alt=\"\"\n            >\n            <span class=\"el-upload-list__item-actions\">\n        <span\n          class=\"el-upload-list__item-preview\"\n          @click=\"handlePictureCardPreview(file)\"\n        >\n          <i class=\"el-icon-zoom-in\"></i>\n        </span>\n        <span\n          v-if=\"!disabled\"\n          class=\"el-upload-list__item-delete\"\n          @click=\"handleRemove(file)\"\n        >\n          <i class=\"el-icon-delete\"></i>\n        </span>\n      </span>\n          </div>\n        </el-upload>\n    <!--    放大图-->\n        <el-dialog :visible.sync=\"dialogVisible\">\n          <img width=\"100%\" :src=\"dialogImageUrl\" alt=\"\">\n        </el-dialog>\n      </el-form-item>\n\n      <el-form-item class=\"form-btn\">\n        <el-button type=\"primary\" @click=\"resetForm('bookForm')\">取 消</el-button>\n        <el-button type=\"primary\" @click=\"submitForm('bookForm')\">发 布</el-button>\n      </el-form-item>\n    </el-form>\n  </el-container>\n</el-container>\n    </div>\n</template>\n\n<script>\n  import {addbook} from \"../axios/api\";\n  import pageheader from \"../components/pageheader\";\n  import pagefooter from \"../components/footer\";\n  export default {\n        name: \"addbookbyuser\",\n    components: {\n      pageheader,\n      pagefooter\n    },\n      data:function () {\n          return{\n            bookForm:{\n              code:'',\n              userId:'',\n              iSBN:'',\n              bookName:'',\n              author:'',\n              imgurl:'',\n              publisher:'',\n              publishTime:'',\n              price:'',\n              discount:'',\n              upDate:'',\n              storage:'',\n              tips:'',\n              imgList:[],\n            },\n            dialogImageUrl: '',\n            dialogVisible: false,\n            disabled: false,\n            hideUploadAdd:false,\n            filelist:[]\n          }\n      },\n    computed:{\n          newprice:function () {\n            return this.bookForm.price*this.bookForm.discount;\n          }\n    },\n      methods: {\n        newTransformBase64(list, callback, errorCallback) {\n          let promise = [];\n          list.forEach((item, index) => {\n            let p = new Promise((resolve, reject) => {\n              let reader = new FileReader();\n              let obj;\n              if (item.raw) {\n                reader.readAsDataURL(item.raw);\n                reader.onload = function (e) {\n                  obj = reader.result;\n                  resolve(obj);\n                }\n              }\n            })\n            promise.push(p);\n          })\n\n          Promise.all(promise).then(results => {\n            callback(results);\n          }).catch(error => {\n            errorCallback && errorCallback();\n            this.$message.error(\"错误\");\n          });\n        },\n        // 上传change事件\n        handleAddChange(file, fileList){\n          // 图片大小限制\n          const isLt20M = file.size / 1024 / 1024 < 20;\n          if (!isLt20M) {\n            this.$message.error(\"上传图片大小不能超过 20MB!\");\n            fileList.splice(-1, 1);\n          } else {\n            this.filelist.push(file);\n            }\n        },\n\n        // 多选大于限制个数时做提示\n        handleExceed(file, fileList) {\n          // 上传文件>=限制个数时隐藏上传组件\n          if (fileList.length >= 5) {\n            this.hideUploadAdd = true;\n            this.$message({\n              message: \"上传文件超出限制个数！\",\n              type: \"warning\"\n            });\n          }\n        },\n        handleRemove(file,fileList) {\n         this.$refs[upload].clearFiles(file)\n        },\n        handlePictureCardPreview(file) {\n          this.dialogImageUrl = file.url;\n          this.dialogVisible = true;\n        },\n        submitForm(formname){\n          this.newTransformBase64(this.filelist, (results) => {\n              this.bookForm.imgList=results;\n           // console.log(JSON.stringify(this.bookForm))\n            var bookparam=this.bookForm;\n            this.bookForm.userId=this.$store.state.user.userId;//添加该书本的用户\n            console.log(this.$store.state.user.userId);\n            //this.bookForm.upDate=new Date();//获得当前系统时间\n           // this.bookForm.upDate=this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss');\n            this.bookForm.code='10001';//图书类型id\n            var bookparam=this.bookForm;\n            addbook(bookparam).then(res=>{\n              if(res.data!=0){\n                console.log(res.data)\n                this.$message({\n                  showClose:true,\n                  message:\"发布成功\",\n                  type:'success'\n                })\n                //this.bookForm.upDate=this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss');\n                //跳转到详情页\n                this.$router.push({\n                  name:'bookdetail',\n                  params:{\n                    bookId:res.data\n                  }\n                })\n              }\n            })\n          })\n\n\n        },\n        resetForm(formName) {\n          this.$refs[formName].resetFields();\n        },\n      }\n    }\n</script>\n\n<style scoped>\n  .user-info{\n    border:1px solid;\n    width: 220px;\n    height: 600px\n  }\n.book-form >>> .el-form-item .el-input{\n  width: 300px;\n  margin: 0 auto;\n}\n.book-form{\n  padding-top:20px;\n  width: 1000px;\n  margin: 0 auto;\n  border: 1px solid\n}\n.search{\n  width: 1226px;\n  margin: 0 auto;\n}\n.history{\n  position: relative;\n  margin-left: 210px;\n  padding-top: 0px;\n}\n.his-li{\n  float: left;\n  color: #333333;\n  font-size: 12pt;\n  margin-left: 8px;\n  opacity: 0.8;\n}\n.banner{\n  width: 100%;\n  background-color: #E9EBE7;\n  border: 1px solid #E9EBE7;\n}\n.search-input{\n  margin-left:450px;\n  margin-top: 2px;\n}\n</style>\n"],"sourceRoot":""}]);

