 #ff6662\">￥{{book.price*book.discount}}</span>\n                    </li>\n                  </ul>\n                </li>\n\n              </div>\n            </ul>\n\n        </el-container>\n      </el-container>\n\n\n    </div>\n</template>\n\n<script>\n  import {findbooktop10} from \"../axios/api\"\n    export default {\n      name: \"booklist\",\n      data: function () {\n        return {\n          bookdata:\"\",\n        }\n      },\n      mounted() {\n        findbooktop10().then(res=>{\n\n          console.log(res.data)\n          var imglist=[];\n          res.data.forEach(data=>{\n            imglist=data.imgurl.split(\",\");\n            data.imgurl=imglist[0]\n          })\n          this.bookdata=res.data;\n          console.log(this.bookdata[0].imgurl)\n        })\n      },\n      methods:{\n        toDetail(bookId){\n          this.$router.push({\n            name:'bookdetail',\n            params:{\n              bookId:bookId\n            }\n          })\n        }\n      }\n    }\n</script>\n\n<style scoped>\n  .el-divider--horizontal{\n    margin: 8px 0;\n  }\n  .booklist-header{\n    font-size: 22px;\n  }\n\n  .goods{\n    overflow: hidden;\n    width:1114px;\n  }\n  .one{\n    width:230px;\n    height:320px;\n    float: left;\n    margin: 1px;\n    border: 1px solid #E3E3E1;\n  }\n  .one > ul >li{\n    margin-top: 10px;\n  }\n</style>\n"],"sourceRoot":""}]);

