A;IACA,gBAAA;IACA,iBAAA;IACA,aAAA;CACA;AACA;KACA,wBAAA;IACA,mBAAA;IACA,gBAAA;IACA,mBAAA;IACA,iBAAA;CACA;AACA;IACA,sBAAA;CACA;AACA;EACA,yBAAA;KACA,oBAAA;MACA,gBAAA;IACA,eAAA;EACA,YAAA;EACA,aAAA;IACA,kBAAA;IACA,mBAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;IACA,eAAA;IACA,oBAAA;CAEA","file":"register.vue","sourcesContent":["<template>\n    <div class=\"register\">\n      <el-container direction=\"vertical\">\n          <!--   头部和banner-->\n          <el-main><pageheader></pageheader></el-main>\n          <div class=\"banner\">\n            <el-container direction=\"horizontal\" class=\"search\">\n              <el-main>\n                <el-image :src=\"require('@/assets/img/logo.png')\" style=\"width: 230px;height: 88px\"></el-image>\n              </el-main>\n              <el-container direction=\"vertical\">\n                <el-main class=\"search-input\">\n                  <el-input placeholder=\"请输入内容\" style=\"width:320px\"></el-input> <el-button><span>搜索</span></el-button></el-main>\n                <el-main class=\"history\">\n                  <ul class=\"history\">\n                    <li class=\"his-li\">莫言</li>\n                    <li class=\"his-li\">余光中</li>\n                    <li class=\"his-li\">毛姆</li>\n                    <li class=\"his-li\">追风筝的人</li>\n                    <li class=\"his-li\">莫言</li>\n                    <li class=\"his-li\">余光中</li>\n                  </ul>\n\n                </el-main>\n              </el-container>\n            </el-container>\n          </div>\n\n\n        <el-main style=\"width: 100%\">\n          <div class=\"reg-form\">\n      <el-form :model=\"ruleForm\" status-icon :rules=\"rules\" ref=\"ruleForm\" label-width=\"100px\" class=\"demo-ruleForm\">\n        <el-form-item label=\"头像\" class=\"userImg\">\n          <el-upload\n            class=\"avatar-uploader\"\n            action\n            ::limit=\"1\"\n            :show-file-list=\"false\"\n            :on-change=\"handleChange\"\n            :before-upload=\"beforeUpload\"\n            accept=\"image/png, image/gif, image/jpg, image/jpeg\">\n            <img v-if=\"ruleForm.userImg\" :src=\"ruleForm.userImg\" class=\"avatar\">\n            <i v-else class=\"el-icon-plus avatar-uploader-icon\"></i>\n          </el-upload>\n        </el-form-item>\n        <el-form-item label=\"账号\">\n          <el-input v-model=\"ruleForm.account\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"昵称\">\n          <el-input v-model.number=\"ruleForm.userName\"></el-input>\n        </el-form-item>\n        <el-form-item\n          label=\"邮箱\">\n          <el-input v-model=\"ruleForm.email\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"性别\">\n          <el-radio v-model=\"ruleForm.sex\" label=\"0\">男</el-radio>\n          <el-radio v-model=\"ruleForm.sex\" label=\"1\">女</el-radio>\n        </el-form-item>\n        <el-form-item label=\"密码\">\n          <el-input type=\"password\" v-model=\"ruleForm.password\" auto-complete=\"off\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"确认密码\">\n          <el-input type=\"password\" v-model=\"ruleForm.checkpwd\" auto-complete=\"off\"></el-input>\n        </el-form-item>\n        <el-form-item class=\"form-btn\">\n        <el-button type=\"primary\" @click=\"resetForm('ruleForm')\">取 消</el-button>\n        <el-button type=\"primary\" @click=\"submitForm('ruleForm')\">注 册</el-button>\n        </el-form-item>\n      </el-form>\n          </div>\n        </el-main>\n        <pagefooter></pagefooter>\n      </el-container>\n    </div>\n</template>\n\n<script>\n  import pageheader from \"../components/pageheader\";\n  import pagefooter from \"../components/footer\";\n  import {register} from \"../axios/api\";\n  export default {\n        name: \"register\",\n      components: {\n        pageheader,\n        pagefooter\n      },\n      data() {\n        var checkNum = (rule, value, callback) => {\n          if (!value) {\n            return callback(new Error('账号不能为空'));\n          }\n          setTimeout(() => {\n            if (!Number.isInteger(value)) {\n              callback(new Error('请输入数字值'));\n            } else {\n              var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;\n              if (!myreg.test(value) ) {\n                callback(new Error('请输入正确的手机号码'));\n              } else {\n                callback();\n              }\n\n            }\n          }, 1000);\n        };\n        var validatePass = (rule, value, callback) => {\n          if (value === '') {\n            callback(new Error('请输入密码'));\n          } else {\n\n            callback();\n          }\n        };\n        return {\n          ruleForm: {\n            userId:'',\n            account:'',\n            password: '',\n            userName: '',\n            sex:'',\n            email:'',\n            role:'2',\n            createDate:'',\n            disabled:'1',\n            userImg:'',\n          },\n          checkpwd:'',\n          rules: {\n            password: [\n              { validator: validatePass, trigger: 'blur' }\n            ],\n\n            userName: [\n              { validator: checkNum, trigger: 'blur' }\n            ]\n          },\n        };\n      },\n      methods: {\n          submitForm(formName) {\n          this.$refs[formName].validate((valid) => {\n            if (valid) {\n              //提交成功做的动作\n             console.log(\"注册信息\"+JSON.stringify(this.ruleForm));\n              this.ruleForm.createDate=new Date().getDay();\n              var formparams=this.ruleForm;\n              register(formparams).then(res=>{\n                console.log(res);\n                if(res.status==200){\n                  this.$message({\n                    type: 'success',\n                    message: '注册成功'\n                  })\n                  this.$router.push(\"/index\");\n                }else{\n                  this.$message.error('注册异常');\n                }\n\n              })\n              //表单验证失败\n            } else {\n              console.log('error submit!!');\n              return false;\n            }\n          });\n        },\n        resetForm(formName) {\n          this.$refs[formName].resetFields();\n        },\n        handleChange(file) {\n          const isLt2M = file.size / 1024 / 1024 < 2;\n          if (isLt2M) {\n            // uploadImgToBase64()返回一个Promise对象，通过.then()获取其数据。其中data.result是图片转成的base64值\n            this.uploadImgToBase64(file.raw).then((data) => {\n              this.ruleForm.userImg= data.result;\n            });\n          } else {\n            this.$message.error(\"上传图片大小不能超过 2MB!\");\n          }\n        },\n        uploadImgToBase64(file) {\n          // 核心方法，将图片转成base64字符串形式\n          return new Promise((resolve, reject) => {\n            const reader = new FileReader();\n            reader.readAsDataURL(file);\n            reader.onload = function () {\n              // 图片转base64完成后返回reader对象\n              resolve(reader);\n            };\n            reader.onerror = reject;\n          });\n        },\n        beforeUpload(file) {\n          return false;\n        },\n      }\n    }\n</script>\n\n<style scoped>\n  .form-btn >>>.el-button{\n    width: 120px;\n    margin-left: 20px;\n    background-color: #E0B41B;\n    border: 0;\n  }\n  .demo-ruleForm >>> .el-input{\n    width: 320px;\n  }\n  .reg-form{\n    border: 2px solid #666752;\n    background-color: #EAEFF0;\n    border-radius: 10px;\n    padding-top: 20px;\n    width: 520px;\n    margin: 0 auto;\n    margin-top: 30px;\n  }\n  .search{\n    width: 1226px;\n    margin: 0 auto;\n  }\n  .history{\n    position: relative;\n    margin-left: 210px;\n    padding-top: 0px;\n  }\n  .banner{\n    width: 100%;\n    background-color: #E9EBE7;\n    border: 1px solid #E9EBE7;\n  }\n  .search-input{\n    margin-left:450px;\n    margin-top: 2px;\n  }\n  .his-li{\n    float: left;\n    color: #333333;\n    font-size: 12pt;\n    margin-left: 8px;\n    opacity: 0.8;\n  }\n   .avatar-uploader .el-upload {\n     border: 1px solid white;\n    border-radius: 6px;\n    cursor: pointer;\n    position: relative;\n    overflow: hidden;\n  }\n.avatar-uploader .el-upload:hover {\n    border-color: #409EFF;\n  }\n.avatar-uploader-icon {\n  border: 2px dashed white;\n     border-radius: 10px;\n      font-size: 28px;\n    color: #8c939d;\n  width: 90px;\n  height: 90px;\n    line-height: 90px;\n    text-align: center;\n  }\n.avatar {\n  width: 90px;\n  height: 90px;\n    display: block;\n    border-radius: 10px;\n\n  }\n\n</style>\n"],"sourceRoot":""}]);

