        </span>\n                  <el-dropdown-menu slot=\"dropdown\">\n                    <el-dropdown-item icon=\"el-icon-user\">\n                      <span>个人中心</span>\n                    </el-dropdown-item>\n                    <el-dropdown-item icon=\"el-icon-user\">\n                      <span @click=\"toaddbook\">发布闲置书籍</span>\n                    </el-dropdown-item>\n                    <el-dropdown-item icon=\"el-icon-user\">\n                      <span @click=\"toshoppingcar\">购物车</span>\n                    </el-dropdown-item>\n                    <el-dropdown-item icon=\"el-icon-circle-close\">\n                      <span @click=\"toorderlist\">我的订单</span>\n                    </el-dropdown-item>\n                    <el-dropdown-item icon=\"el-icon-circle-close\">\n                      <span @click=\"logout\">退出登录</span>\n                    </el-dropdown-item>\n                  </el-dropdown-menu>\n                </el-dropdown>\n               </template> </li>\n\n            </ul>\n          </el-main>\n        </el-container>\n        <!--仔细阅读Dialog的各个属性参数，会影响到布局排版，例如遇到了一个大坑就是没有设置:append-to-body='true'，导致遮罩遮盖整个页面，:lock-scroll=\"false\"没有设置的话，点击前后会感觉到头部导航栏的移动，体验性很不好！！还有设置dialog的宽度width=\"40%\"前面不用加冒号：-->\n        <el-dialog title=\"登录\" :visible.sync=\"dialogFormVisible\"  center :append-to-body='true' :lock-scroll=\"false\" width=\"30%\">\n          <!--这里可以写各种登录表单-->\n          <el-form :model=\"ruleForm2\" status-icon :rules=\"rules2\" ref=\"ruleForm2\" label-width=\"100px\" class=\"demo-ruleForm\">\n            <el-form-item label=\"账   号\" prop=\"num\">\n              <el-input v-model.number=\"ruleForm2.account\" style=\"width:250px\"></el-input>\n            </el-form-item>\n\n            <el-form-item label=\"密   码\" prop=\"pass\">\n              <el-input type=\"password\" v-model=\"ruleForm2.password\" auto-complete=\"off\" style=\"width:250px\"></el-input>\n            </el-form-item>\n\n            <el-form-item label=\"记住密码\" prop=\"delivery\" class=\"from-switch\">\n              <el-switch v-model=\"delivery\" active-color=\"#E0B41B\"></el-switch>\n            </el-form-item>\n          </el-form>\n          <div slot=\"footer\" class=\"dialog-footer\">\n            <el-button @click=\"dialogFormVisible = false,resetForm('ruleForm2')\">取 消</el-button>\n            <el-button type=\"primary\" @click=\"submitForm('ruleForm2')\">登 录</el-button>\n          </div>\n        </el-dialog>\n      </div>\n\n\n  </div>\n</template>\n<script>\n  import {login} from '../axios/api'\n  export default {\n    name: \"pageheader\",\n    data() {\n      /*插入form方法*/\n      var checkNum = (rule, value, callback) => {\n        if (!value) {\n          return callback(new Error('账号不能为空'));\n        }\n        setTimeout(() => {\n          if (!Number.isInteger(value)) {\n            callback(new Error('请输入数字值'));\n          } else {\n            var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;\n            if (!myreg.test(value) ) {\n              callback(new Error('请输入正确的手机号码'));\n            } else {\n              callback();\n            }\n\n          }\n        }, 1000);\n      };\n      var validatePass = (rule, value, callback) => {\n        if (value === '') {\n          callback(new Error('请输入密码'));\n        } else {\n\n          callback();\n        }\n      };\n      /*插入form方法*/\n\n      return {\n        userImgURl:'',\n        delivery: false,\n        /*插入form方法*/\n        /*设定规则指向*/\n        ruleForm2: {\n          password: '',\n          account: '',\n\n        },\n        rules2: {\n          password: [\n            { validator: validatePass, trigger: 'blur' }\n          ],\n\n          account: [\n            { validator: checkNum, trigger: 'blur' }\n          ]\n        },\n\n        /*插入form方法*/\n        dialogFormVisible:false,\n        dialogTableVisible: false,\n        form: {\n          name: '',\n          type: [],\n          resource: '',\n          desc: ''\n        },\n        formLabelWidth: '120px'\n      };\n    },\n    //页面加载调用获取cookie值\n    mounted() {\n      this.getCookie();\n    },\n    methods: {\n      resetForm(formName) {\n        this.$refs[formName].resetFields();\n      },\n      submitForm(formName) {\n        this.$refs[formName].validate((valid) => {\n          if (valid) {\n            login(this.ruleForm2).then(res=>{\n              if(res.data!=\"\"){\n                //判断复选框是否被勾选 勾选则调用配置cookie方法\n                console.log(res.data);\n                if (this.delivery == true) {\n                  console.log(\"checked == true\");\n                  //传入账号名，密码，和保存天数3个参数\n                  this.setCookie(this.ruleForm2.account, this.ruleForm2.password, 7);\n                } else {\n                  console.log(\"清空Cookie\");\n                  //清空Cookie\n                  this.clearCookie();\n                }\n                this.$store.commit('setToken',true);\n                this.dialogFormVisible = false;\n                this.userImgURl=\"http://localhost:88/upload/\"+res.data.userImg;\n                res.data.userImg=this.userImgURl;\n                this.$store.commit('setUser',res.data);\n                console.log(this.userImgURl)\n                console.log(res.data)\n                this.$message({\n                  type: 'success',\n                  message: '欢迎您：'+this.$store.state.user.userName,\n                  showClose: true,\n                });\n              }else{\n                this.$message({\n                  showClose: true,\n                  message: '登录失败，用户名或密码错误',\n                  type: 'error'\n                });\n                this.ruleForm2.account=\"\";\n                this.ruleForm2.password=\"\";\n                this.delivery=false;\n              }\n            })\n          } else {\n            this.$message.error(\"登录异常\");\n            return false;\n          }\n        });\n      },\n      //设置cookie\n      setCookie(c_name, c_pwd, exdays) {\n        var exdate = new Date(); //获取时间\n        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //保存的天数\n        //字符串拼接cookie\n        window.document.cookie = \"account\" + \"=\" + c_name + \";path=/;expires=\" + exdate.toGMTString();\n        window.document.cookie = \"password\" + \"=\" + c_pwd + \";path=/;expires=\" + exdate.toGMTString();\n      },\n      //读取cookie\n      getCookie: function () {\n        if (document.cookie.length > 0) {\n          var arr = document.cookie.split('; '); //这里显示的格式需要切割一下自己可输出看下\n          for (var i = 0; i < arr.length; i++) {\n            var arr2 = arr[i].split('='); //再次切割\n            //判断查找相对应的值\n            if (arr2[0] == 'account') {\n              this.ruleForm2.account = arr2[1]; //保存到保存数据的地方\n            } else if (arr2[0] == 'password') {\n              this.ruleForm2.password = arr2[1];\n            }\n          }\n        }\n      },\n      //清除cookie\n      clearCookie: function () {\n        this.setCookie(\"account\", \"password\", -1); //修改2值都为空，天数为负1天就好了\n        this.$store.commit('setToken',false);\n      },\n      loginCheck: function () {\n        if (this.$store.state.token==true) {\n          this.$message({\n            showClose: true,\n            message: '您已经登录了！'\n          });\n        } else {\n          this.dialogFormVisible = true;\n        }\n      },\n      logout: function () {\n        if (document.cookie.length > 0) {\n          this.clearCookie();\n          this.$message({\n            showClose: true,\n            message: '退出登录',\n            type: \"success\"\n          });\n        } else {\n          this.$message({\n            showClose: true,\n            message: '您还未登录',\n          });\n        }\n\n      },\n      toshoppingcar(){\n        this.$router.push('/shoppingcar')\n      },\n\n      toRegister(){\n        this.$router.push('/register')\n      },\n      toIndex(){\n        this.$router.push('/')\n      },\n      toaddbook(){\n        this.$router.push('/addbookbyuser')\n      },\n      toorderlist(){\n        this.$router.push('/orderlist')\n      }\n    }\n  }\n</script>\n\n<style scoped>\n  .dialog-footer >>>.el-button--primary{\n    background-color: #E0B41B;\n    border: 0;\n\n  }\n  .dialog-footer >>> .el-button{\n    width: 120px;\n  }\n\n#pageheader{\n  width: 100%;\n  background-color: #666752;\n  color: white;\n  height: 45px;\n}\n  .top{\n    width: 1226px;\n    margin:0 auto;\n  }\n  ul{\n    list-style-type:none;\n    margin:0px;\n    width: 200px;\n    position: relative;\n    left:360px;\n  }\n  ul li{\n    float: left;\n    margin-left: 8px;\n    margin-top:10px;\n  }\n  ul > .img-no-li{\n    margin-top: 1px;\n  }\n</style>\n"],"sourceRoot":""}]);

