import Vue from 'vue'
import Router from 'vue-router'
import index from '../page/index'
import bookdetail from "../page/bookdetail";
import shoppingcar from "../page/shoppingcar";
import register from "../page/register";
import addbookbyuser from '../page/addbookbyuser'
import userorder from "../page/userorder";
import orderlist from "../page/orderlist";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/bookdetail',
      name: 'bookdetail',
      component: bookdetail
    }
    ,
    {
      path: '/shoppingcar',
      name: 'shoppingcar',
      component: shoppingcar
    }
    ,
    {
      path: '/register',
      name: 'register',
      component: register
    }
    ,
    {
      path: '/addbookbyuser',
      name: 'addbookbyuser',
      component: addbookbyuser
    }
    ,
    {
      path: '/userorder',
      name: 'userorder',
      component: userorder
    }
    ,
    {
      path: '/orderlist',
      name: 'orderlist',
      component: orderlist
    }
  ]
})



